@import url(https://fonts.googleapis.com/css2?family=Architects+Daughter&display=swap);
html,body,main,div,span,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video,input,textarea{margin:0;padding:0;border:0;background:none;font-size:100%;font-weight:400;-webkit-font-smoothing:antialiased;-webkit-tap-highlight-color:rgba(20,23,28,0)}html{overflow-x:hidden;height:100%}*{outline:none;box-sizing:border-box;text-rendering:optimizeLegibility}*:active,*:hover,*:focus{outline:0;outline-offset:0}*::-moz-focus-inner{border:0}body{font-size:16px;height:100%}a{text-decoration:none;color:black}img{display:block;max-width:100%}label,a,input,textarea{outline:none;-webkit-tap-highlight-color:rgba(0,0,0,0);box-shadow:none}figure{margin:0}button{border:none;cursor:pointer;outline:none;background:transparent;padding:0}

.todoItem{display:-webkit-flex;display:flex}.todoItem p{text-align:left;margin:30px 10px;font-size:1.4rem;font-weight:300;color:#494a4b;font-family:"Architects Daughter",sans-serif;max-width:250px;min-width:200px}.priority{min-width:30px !important}

.reset{border:1px solid #fdcb6e;border-radius:8px;width:50px;height:40px;font-family:"Architects Daughter",sans-serif;font-size:1.2rem;margin-top:20px;color:red;font-weight:700;cursor:pointer}.reset:hover{background-color:#c6cacf}

.important{border:1px solid #fdcb6e;border-radius:8px;width:50px;height:40px;font-family:"Architects Daughter",sans-serif;font-size:1.2rem;margin-top:20px;color:green;font-weight:700;cursor:pointer}.important:hover{background-color:#c6cacf}

.filter-container{display:-webkit-flex;display:flex;-webkit-flex-wrap:wrap;flex-wrap:wrap}.filter-container input{outline:none;border:3px dashed #fdcb6e;background-color:transparent;font-family:"Architects Daughter",sans-serif;font-size:1.3rem;padding:10px 15px;width:45%;color:#494a4b;border-radius:8px;margin-right:15px}.filter-container button{border:1px solid #fdcb6e;border-radius:8px;width:100px;height:50px;font-family:"Architects Daughter",sans-serif;font-size:0.9rem;margin-right:15px;cursor:pointer}.filter-container button:hover{background-color:#c6cacf}.activeBtn{background-color:#c6cacf}.inactiveBtn{background-color:transparent}

h1{width:80%;margin:30px 0;background-color:#fdc66e;border-radius:10px;border-radius:20% 5% 20% 5%/5% 20% 25% 20%;padding:2rem 1.2rem;color:#494a4b;text-align:center;font-weight:700;font-family:"Architects Daughter", sans-serif}

.form-container{display:-webkit-flex;display:flex;-webkit-flex-wrap:wrap;flex-wrap:wrap;margin-top:15px}.form-container input{outline:none;border:3px dashed #db7c3c;background-color:transparent;font-family:"Architects Daughter",sans-serif;font-size:1.3rem;padding:10px 15px;width:45%;color:#494a4b;border-radius:8px;margin-right:15px}.form-container button{border:1px solid #db7c3c;border-radius:8px;width:100px;height:50px;font-family:"Architects Daughter",sans-serif;font-size:0.9rem;margin-right:15px;cursor:pointer}.form-container button:hover{background-color:#c6cacf}

.container{position:fixed;left:0;top:0;right:0;bottom:0;background:#c09332;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center}.transition-loader{position:absolute;top:0;left:0;height:100%;width:100%}.transition-loader-inner{-webkit-transform:translateY(-50%);transform:translateY(-50%);top:50%;position:absolute;width:calc(100% - 200px);color:#FFF;padding:0 100px;text-align:center}.transition-loader-inner label{font-size:1em;padding:1%;opacity:0;display:inline-block}.transition-loader-inner label:after{font-family:FontAwesome;content:'\f111'}.transition-loader-inner label:nth-child(1){-webkit-animation:loader 3s 600ms infinite ease-in-out;animation:loader 3s 600ms infinite ease-in-out}.transition-loader-inner label:nth-child(2){-webkit-animation:loader 3s 500ms infinite ease-in-out;animation:loader 3s 500ms infinite ease-in-out}.transition-loader-inner label:nth-child(3){-webkit-animation:loader 3s 400ms infinite ease-in-out;animation:loader 3s 400ms infinite ease-in-out}.transition-loader-inner label:nth-child(4){-webkit-animation:loader 3s 300ms infinite ease-in-out;animation:loader 3s 300ms infinite ease-in-out}.transition-loader-inner label:nth-child(5){-webkit-animation:loader 3s 200ms infinite ease-in-out;animation:loader 3s 200ms infinite ease-in-out}.transition-loader-inner label:nth-child(6){-webkit-animation:loader 3s 100ms infinite ease-in-out;animation:loader 3s 100ms infinite ease-in-out}@keyframes loader{0%{opacity:0;-webkit-transform:translateX(-300px) scale(1);transform:translateX(-300px) scale(1)}33%{opacity:1;-webkit-transform:translateX(0px) scale(2);transform:translateX(0px) scale(2)}66%{opacity:1;-webkit-transform:translateX(0px) scale(1);transform:translateX(0px) scale(1)}100%{opacity:0;-webkit-transform:translateX(300px) scale(2);transform:translateX(300px) scale(2)}}@-webkit-keyframes loader{0%{opacity:0;-webkit-transform:translateX(-300px)}33%{opacity:1;-webkit-transform:translateX(0px)}66%{opacity:1;-webkit-transform:translateX(0px)}100%{opacity:0;-webkit-transform:translateX(300px)}}

#root{margin:50px auto;padding:16px;min-height:580px;max-width:900px;background-color:#f1f5f8;background-image:radial-gradient(#bfc0c1 7.2%, transparent 0);background-size:40px 40px;border-radius:20px;box-shadow:4px 3px 7px 2px #00000040}

